import React from "react";
import { originOrThumb } from "../../../../utils/func";
import { brandManage as fieldName } from '../../../../const/fieldName'

export default [
  {
    title: '', dataIndex: fieldName.cover, render: cover => (
      <img src={originOrThumb(cover, 'thumb')} alt="" height="64px" />
    )
  },
  { title: '', dataIndex: fieldName.title, width: 200 },
  { title: '', dataIndex: fieldName.updateTime, width: 200 },
  { title: '', dataIndex: fieldName.createTime, width: 200 },
]



